<script>
import SlickSimple from "@/components/SlickSimple.vue";
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";

Vue.use(VueAxios, axios);
export default {
  components: {
    SlickSimple,
  },
  data() {
    return {
      top3_list: [
        {
          id: "",
          title: "",
          img: "",
          excerpt: "",
        },
        {
          id: "",
          title: "",
          img: "",
          excerpt: "",
        },
        {
          id: "",
          title: "",
          img: "",
          excerpt: "",
        },
      ],
      top_item: [],
    };
  },
  mounted() {
    const url = "assets/json/posts_list.json";
    axios.get(url).then((response) => {
      this.top3_list = response.data.slice(response.data.length - 3);
      for (let i = 0; i < this.top3_list.length; i++) {
        let item = this.top3_list[i];
        if (item.excerpt.length >= 150) {
          item.excerpt = item.excerpt.substring(0, 150);
          let lastIndex = item.excerpt.lastIndexOf(" "); // позиция последнего пробела
          item.excerpt = item.excerpt.substring(0, lastIndex) + "..."; // обрезаем до последнего слова
        }
      }
      this.getTopItem(this.top3_list[0].id);
    });
    let linkContainer = document.getElementById("news-list");
    let links = linkContainer.getElementsByClassName("news-item");

    for (var i = 0; i < links.length; i++) {
      links[i].addEventListener("click", function () {
        let current = document.getElementsByClassName("active");
        current[0].className = current[0].className.replace("active", "");
        this.className += " active";
      });
    }
  },
  methods: {
    getTopItem(id) {
      for (let i = 0; i < this.top3_list.length; i++) {
        let item = this.top3_list[i];
        if (item.id == id) {
          this.top_item = item;
        }
      }
    },
  },
};
</script>
