<template>
  <div class="thumb-example">
    <VueSlickCarousel
      ref="c3"
      :asNavFor="$refs.c3"
      :slidesToShow="4"
      :focusOnSelect="true"
      :arrows="true"
      :infinite="true"
      :autoplay="true"
      :autoplaySpeed="2000"
      :pauseOnDotsHover="true"
      :pauseOnFocus="true"
      :pauseOnHover="true"
    >
      <div class="item" v-for="item in menuSp" :key="item.id">
        <div class="cards">
          <a class="sm-img" href="#">
            <img :src="item.img" alt="" />
            <div class="price">{{ item.price }}</div>
            <div class="old-price">{{ item.oldPrice }}</div>
          </a>
          <div class="sm-title">{{ item.name }}</div>
          <p>
            {{ item.about }}
          </p>
        </div>
      </div>
    </VueSlickCarousel>
  </div>
</template>
 
<script>
import VueSlickCarousel from "vue-slick-carousel";
import "vue-slick-carousel/dist/vue-slick-carousel.css";
// optional style for arrows & dots
import "vue-slick-carousel/dist/vue-slick-carousel-theme.css";
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";

Vue.use(VueAxios, axios);

export default {
  name: "SlickSimple",
  components: { VueSlickCarousel },
  data() {
    return {
      menuSp: [
        {
          id: " ",
          img: " ",
          name: " ",
          about: " ",
          price: " ",
          oldPrice: "",
          group: " ",
        },
      ],
    };
  },
  mounted() {
    const url = "assets/json/sp_menu.json";

    axios.get(url).then((response) => {
      this.menuSp = response.data;
    });
  },
};
</script> 

<style>
.slick-arrow:before {
  font-size: 50px;
  color: #da5252;
}
.slick-arrow :before:hover {
  opacity: 1;
}
.slick-prev,
.slick-next {
  top: calc(50% - 26px);
}
.slick-prev {
  left: -60px;
}
.slick-next {
  right: -30px;
}
</style>